import createDecorator from "final-form-calculate";

const valuesChecker = value => {
  if (value === undefined || value === null || isNaN(parseFloat(value))) {
    return 0
  } else {
    return Math.round(value * 100) / 100
  }
}

const multiplier = (a, b) =>
  Math.round((valuesChecker(a) * valuesChecker(b)) * 100) / 100

const divider = (a, b) => {
  const result = Math.round((valuesChecker(a) / valuesChecker(b)) * 100) / 100;
  return isFinite(result) && !isNaN(result) ? result : 0
}

const feeder = (a, b) =>
  Math.round((valuesChecker(a) + valuesChecker(b)) * 100) / 100


export const calculateShares = (existingShares, numberOfSharesBeforeIncrease) =>
  divider(existingShares, numberOfSharesBeforeIncrease)

export const calculateSubscriptionAmount = (addedShares, shareSubscriptionPrice) =>
  multiplier(addedShares, shareSubscriptionPrice)


export const calculator = createDecorator(
  {
    field: "numberOfSharesBeforeIncrease",
    updates: {
      totalExistingShareCapital: (ignoredValue, allValues = {}) => {
        const {numberOfSharesBeforeIncrease, denomination} = allValues;
        return multiplier(numberOfSharesBeforeIncrease, denomination)
      },
      numberOfSharesAfterIncrease: (ignoredValue, allValues = {}) => {
        const {numberOfNewShares, numberOfSharesBeforeIncrease} = allValues;
        return feeder(numberOfSharesBeforeIncrease, numberOfNewShares)
      }
    }
  },
  {
    field: "denomination",
    updates: {
      totalExistingShareCapital: (ignoredValue, allValues = {}) => {
        const {numberOfSharesBeforeIncrease, denomination} = allValues;
        return multiplier(numberOfSharesBeforeIncrease, denomination)
      },
      sumIncreasedShareCapital: (ignoredValue, allValues = {}) => {
        const {numberOfNewShares, denomination} = allValues;
        return multiplier(numberOfNewShares, denomination)
      },
    }
  },
  {
    field: "numberOfNewShares",
    updates: {
      sumIncreasedShareCapital: (ignoredValue, allValues = {}) => {
        const {numberOfNewShares, denomination} = allValues;
        return multiplier(numberOfNewShares, denomination)
      },
      numberOfSharesAfterIncrease: (ignoredValue, allValues = {}) => {
        const {numberOfNewShares, numberOfSharesBeforeIncrease} = allValues;
        return feeder(numberOfSharesBeforeIncrease, numberOfNewShares)
      },
      perMoneyValuation: (ignoredValue, allValues = {}) => {
        const {numberOfNewShares, shareSubscriptionPrice} = allValues;
        return multiplier(numberOfNewShares, shareSubscriptionPrice)
      },
    }
  },
  {
    field: "totalExistingShareCapital",
    updates: {
      totalShareCapital: (ignoredValue, allValues = {}) => {
        const {totalExistingShareCapital, sumIncreasedShareCapital} = allValues;
        return feeder(totalExistingShareCapital, sumIncreasedShareCapital)
      },
    }
  },
  {
    field: "sumIncreasedShareCapital",
    updates: {
      totalShareCapital: (ignoredValue, allValues = {}) => {
        const {totalExistingShareCapital, sumIncreasedShareCapital} = allValues;
        return feeder(totalExistingShareCapital, sumIncreasedShareCapital)
      },
    }
  },
  {
    field: "generalMeetingTableValues",
    updates: {
      numberOfNewShares: (ignoredValue, allValues = {}) => {
        const {generalMeetingTableValues = []} = allValues;
        return generalMeetingTableValues.length > 0 ? generalMeetingTableValues
          .map(({addedShares}) => addedShares ? valuesChecker(addedShares) : 0)
          .reduce((acc, val) => feeder(acc, val)) : 0
      },
    }
  },
  {
    field: "perMoneyValuation",
    updates: {
      shareSubscriptionPrice: (ignoredValue, currentValues) => {
        const {perMoneyValuation, numberOfNewShares} = currentValues;
        return divider(perMoneyValuation, numberOfNewShares)
      },
    }
  },
);