import React from "react";

export const columns = [
  {
    Header: 'Shareholder',
    accessor: 'shareholder',
    editable: true
  },
  {
    Header: '# existing shares',
    accessor: 'existingShares',
    editable: true,
    type: "number",
    style: {
      width: "120px"
    }
  },
  {
    Header: '# new shares',
    accessor: 'addedShares',
    editable: true,
    type: "number",
    style: {
      width: "100px"
    }
  },
  {
    Header: 'represented with',
    accessor: 'representedBy',
    editable: true
  },
  {
    Header: '% shares',
    accessor: 'shares',
    type: "number",
    style: {
      width: "80px"
    }
  },
  {
    Header: 'subscription amount',
    accessor: 'subscriptionAmount',
    type: "number",
    style: {
      width: "150px"
    }
  },
];